html,
body {
    
    min-height: 100vh;
    padding: 0;

  }

  span {

  color: #03071E;
  
  }

.card-title{
  color: #03071E;
  text-decoration: none;
  

}

.skills{
    
  display: flex;
  justify-content: center;
  padding-top: 10px;
}
.codingskills {
  padding-top: 40px;
  justify-content: center;
  
}




   @media screen and (max-width : 576px) {
  
    .skills{
    
      display: flex;
      justify-content: center;
      text-align: center;
      padding-top: 10px;
    }
    .codingskills{
    
      display: flex;
      justify-content: center;
    }
 } 
@media screen and (min-width: 576px) and (max-width: 810px) {

  .skills{
    
    display: flex;
    justify-content: center;
  }
  .codingskills{
    
    display: flex;
    justify-content: center;
  }



  
}
  

  @media screen and (min-width: 811px) and (max-width: 1690px) {
    .skills{
    
      display: flex;
      justify-content: center;
    }
    .codingskills{
    
      display: flex;
      justify-content: center;
    }



    
} 